.modal{
    position: fixed;
    padding: 0;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(20, 30, 39, 0.5);
    transform: scale(1.1);
    opacity: 0;
    visibility: hidden;
    transition: visibility 0.25s ease-in-out, transform 0.25s ease-in-out, opacity 0.25s ease-in-out ; 
}

.modal-content{
    justify-content: center;
    background-color: #444;
    padding: 30px;
    width: 50vw;
    border-radius: 0.5rem;
    color: whitesmoke;
}

.close-button{  
    float: right;
    text-align: center;
    width: 1.3vw;
    cursor: pointer;
    line-height: 24px;
}

.close-button:hover{
    color: greenyellow;
}

.show-modal{
    opacity: 1;
    visibility: visible;
    transform: scale(1.0);
}

#NewDetails{
    margin: 7px auto;
    background-color: #444;
    border: 1px solid chartreuse;
    width: 80%;
}