body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #222;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
    max-width: 500px;
    margin: 30px auto;
    overflow: auto;
    min-height: 300px;
    border: 2px solid chartreuse;
    padding: 30px;
    border-radius: 15px;
}
.add-task-button{
    background-color: chartreuse;
    height: 40px;
    padding: 0 10px;
    border-radius: 5px;
    width: 100%;
    font-size: 16px;
    color: #444;
    font-weight: bold;
    cursor: pointer;
    border: none;
}
.add-task-container{
    margin:15px 0 ;
    width: 100%;
    display: flex;
}

.add-task-input{
    height: 40px;
    padding: 0 10px;
    border-radius: 5px;
    border: none;
    flex: 2 1;
    background-color: #444;
    color: whitesmoke;
    font-size: 16px;
}

.add-task-button-container{
    margin-left: 10px;
    flex: 1 1;
}
.task-container{
    background-color: #444;
    margin: 8px 0;
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    color: whitesmoke;
    align-items: center;
    border-radius: 5px;
    cursor: pointer;
}

.remove-task-button, .see-task-details-button{
    background-color: #444;
    border: none;
    font-size: 24px;
    color: chartreuse;
    cursor: pointer;
}
.task-details-container{
    background-color: #444;
    margin: 15px 0;
    padding: 15px 20px;
    display: flex;
    flex-direction: column;
    color: whitesmoke;
    border-radius: 5px;
}

.task-details-container h2{
    margin-bottom: 5px;
    color: chartreuse;
}

.back-button-container{
    width: 30%;
    margin-bottom: 10px ;
}

#task-details{
    background-color: #444;
    margin: 15px 0;
    padding: 15px 20px;
    display: flex;
    flex-direction: column;
    color: whitesmoke;
    border-radius: 5px;
    border: none;
    overflow: hidden;
}
.modal{
    position: fixed;
    padding: 0;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(20, 30, 39, 0.5);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    opacity: 0;
    visibility: hidden;
    transition: visibility 0.25s ease-in-out, opacity 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;
    transition: visibility 0.25s ease-in-out, transform 0.25s ease-in-out, opacity 0.25s ease-in-out ;
    transition: visibility 0.25s ease-in-out, transform 0.25s ease-in-out, opacity 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out; 
}

.modal-content{
    justify-content: center;
    background-color: #444;
    padding: 30px;
    width: 50vw;
    border-radius: 0.5rem;
    color: whitesmoke;
}

.close-button{  
    float: right;
    text-align: center;
    width: 1.3vw;
    cursor: pointer;
    line-height: 24px;
}

.close-button:hover{
    color: greenyellow;
}

.show-modal{
    opacity: 1;
    visibility: visible;
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
}

#NewDetails{
    margin: 7px auto;
    background-color: #444;
    border: 1px solid chartreuse;
    width: 80%;
}
